<script>
import axios from "axios";
import myCaptcha from "vue-captcha";
import VueRecaptcha from "vue-recaptcha";
import modalAdded from "@/components/commons/CartAdded";
import helper from "@/script/helper";
import Formulario from "@/components/hotel/Formulario";

var iswhitebrand = false;

if (globaldata.whitebrandhotel !== null && globaldata.whitebrandhotel !== "") {
  iswhitebrand = true;
}

const langfile = require("../../assets/lang/content.json");

export default {
  name: "Detalle",
  data: function() {
    return {
      helper: helper,
      detalles: null,
      captchaValidation: globaldata.captchaValidation,
      nocart: globaldata.nocart,
      showwhite: iswhitebrand,
      titular: null,
      comentarios: null,
      ocupacion: [],
      requestPayment: null,
      email: null,
      unmailing: false,
      coupon: null,
      loader: null,
      language: globaldata.language ? globaldata.language : "es",
      multilanguage: langfile.multilanguage,
      selectedServices: null,
      reseolvedCaptcha: false,
      recaptchatoken: globaldata.recaptchatoken,
      termsaccepted: false,
      nobreakdown: globaldata.nobreakdown
    };
  },
  components: {
    "my-captcha": myCaptcha,
    VueRecaptcha,
    modalAdded: modalAdded,
    Formulario:Formulario
  },
  methods: {
    verify: function() {
      // this.status = "submitting";
      this.$refs.recaptcha.execute();
    },
    onCaptchaVerified: function(recaptchaToken) {
      const _this = this;
      _this.status = "submitting";
      _this.$refs.recaptcha.reset();
      _this.book(recaptchaToken);
    },
    onCaptchaExpired: function() {
      this.$refs.recaptcha.reset();
    },
    captchaOk() {
      this.$data.reseolvedCaptcha = true;
    },
    goback(page) {
      switch (page) {
        case "formulario":
          this.$router.push({
            name: "main",
            query: {
              destino: this.$route.query.destino,
              iddestino: this.$route.query.iddestino,
              salida: this.$route.query.salida,
              entrada: this.$route.query.entrada,
              ocupacion: this.$route.query.ocupacion
            }
          });
          break;
        case "dispo":
          this.$router.push({
            name: "hotel_dispo",
            query: {
              destino: this.$route.query.destino,
              iddestino: this.$route.query.iddestino,
              entrada: this.$route.query.entrada,
              salida: this.$route.query.salida,
              ocupacion: this.$route.query.ocupacion
            }
          });
          break;
        case "rates":
          this.$router.push({
            name: "hotel_rates",
            query: {
              destino: this.$route.query.destino,
              iddestino: this.$route.query.iddestino,
              hotelkey: this.$route.query.hotelkey,
              hotelid: this.$route.query.hotelid,
              hotelname: this.$route.query.hotelname,
              address: this.$route.query.address,
              salida: this.$route.query.salida,
              entrada: this.$route.query.entrada,
              ocupacion: this.$route.query.ocupacion,
              stars: this.$route.query.stars,
              keys: this.$route.query.keys
            }
          });
          break;
        default:
      }
    },
    procesar: function(response) {
      if (response.data.statusCode === 200)
        this.$router.push({
          name: "ok",
          query: {
            bookingId: response.data.bookingId,
            availableservices: response.data.availableServices,
            paymenturl: response.data.paymentUrl
          }
        });
      else this.$router.push({ name: "ko", query: { msg: response.data.msg } });
    },
    addtocart: function() {
      var d = this.$data;
      var reference = helper.gettimereference();
      if (this.checkForm()) {
        var cartline = {
          bookingtype: "hotel",
          leadName: d.titular,
          commentsToProvider: d.comentarios,
          privateComments: "",
          email: d.email,
          itemDescription: this.mountdescription(),
          rateKeys: d.ocupacion,
          total: d.detalles.total,
          paymentLines: d.detalles.paymentLines,
          cancellationCosts: d.detalles.cancellationCosts,
          cancellationFreeDate: d.detalles.cancellationFreeDate,
          prices: d.detalles.prices,
          remarks: d.detalles.remarks,
          services: [],
          bookingReference: reference
        };

        var currentCart = localStorage.qtse_cart
          ? JSON.parse(localStorage.getItem("qtse_cart"))
          : [];
        currentCart.push(cartline);
        localStorage.setItem("qtse_cart", JSON.stringify(currentCart));

        this.storageData();

        this.$refs.modalAdded.showModal();
      }
    },
    storageData: function() {
      let maindata = {
        leadName: this.$data.titular,
        contactPhone: this.$data.telefono,
        email: this.$data.email,
        startdate: this.$route.query.entrada,
        enddate: this.$route.query.salida
      };

      localStorage.setItem("qtse_maindatastorage", JSON.stringify(maindata));
    },
    getStorageData: function() {
      let maindata = JSON.parse(localStorage.getItem("qtse_maindatastorage"));
      if (maindata !== null) {
        if (maindata.leadName) {
          this.$data.titular = maindata.leadName;
        }
        if (maindata.contactPhone) {
          this.$data.telefono = maindata.contactPhone;
        }
        if (maindata.email) {
          this.$data.email = maindata.email;
        }
      }
    },
    mountdescription: function() {
      var desc =
        '<div class="title"></b>' +
        this.$route.query.hotelname +
        "</div>" +
        '<div class="line1"> <span><b style="margin-right:0.5em;">' +
        this.multilanguage.from[this.$data.language] +
        ": </b> " +
        helper.formatDatetoview(this.$route.query.entrada) +
        "</span></div>" +
        '<div class="line1"><span><b style="margin-right:0.5em;">' +
        this.multilanguage.to[this.$data.language] +
        ": </b> " +
        helper.formatDatetoview(this.$route.query.salida) +
        "</span></div>";
      for (var i = 0; i < this.$data.detalles.rateKeys.length; i++) {
        desc =
          desc +
          '<div class="line3"> ' +
          this.$data.detalles.rateKeys[i].paxPerRoom +
          " " +
          this.multilanguage.peoplein[this.$data.language] +
          "  " +
          this.$data.detalles.rateKeys[i].roomName +
          " " +
          this.multilanguage.room[this.$data.language] +
          "</div>";
      }
      return desc;
    },
    //cartversion-f
    book: function(recaptchaToken) {
      var esto = this;
      var d = this.$data;
      var reference = helper.gettimereference();

      if (this.checkForm()) {
        d.loader = true;
        axios({
          method: "put",
          url: globaldata.baseurl + "/hotel/booking",
          data: {
            language: d.language,
            rateKeys: this.$data.ocupacion,
            bookingReference: reference,
            captchaToken: recaptchaToken,
            leadName: this.$data.titular,
            commentsToProvider: this.$data.comentarios,
            privateComments: "",
            services: [],
            email: this.$data.email,
            mailingUnwanted: d.unmailing
          }
        })
          .then(function(response) {
            d.loader = false;
            esto.procesar(response);
          })
          .catch(function(error) {
            console.log(error);
            d.loader = null;
            esto.$router.push({ name: "ko", query: { msg: error } });
          });
      }
    },
    setValidationError(field, msgField, msg) {
      var element = document.getElementById(field);

      element.classList.add("is-invalid");
      //   element.classList.add('form-control')
      var att = document.createAttribute("aria-invalid");
      att.value = "true";
      element.setAttributeNode(att);
      element.focus();
      if (msgField !== null && msgField !== " ") {
        var elementmsg = document.getElementById(msgField);

        elementmsg.innerHTML = msg;
      }
    },
    checkForm: function(e) {
      this.cleanErrors();
      var existsErrors = false;

      if (this.$data.detalles.terms && this.$data.nocart) {
        if (this.$data.termsaccepted !== true) {
          this.setValidationError(
            "termandcondcheck",
            "termandcondcheck-msg",
            this.multilanguage.pleaseacceptterms[this.language]
          );
          this.setValidationError(
            "termandcondcheck2",
            "termandcondcheck2-msg",
            this.multilanguage.pleaseacceptterms[this.language]
          );
          existsErrors = true;
        }
      }
      if (this.$data.email === null || this.$data.email === "") {
        this.setValidationError(
          "email",
          "email-msg",
          this.multilanguage.erroremailempty[this.language]
        );
        existsErrors = true;
      } else {
        if (!helper.validateEmail(this.$data.email)) {
          this.setValidationError(
            "email",
            "email-msg",
            this.multilanguage.erroremailnotvalid[this.language]
          );
          existsErrors = true;
        }
      }
      if (this.$data.titular === null || this.$data.titular === "") {
        this.setValidationError(
          "name",
          "name-msg",
          this.multilanguage.errorleadnameempty[this.language]
        );
        existsErrors = true;
      }
      if (existsErrors) {
        return false;
      }
      return true;
    },
    cleanErrors: function(email) {
      var element = document.getElementById("name");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }

      element = document.getElementById("email");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("termandcondcheck");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-check-input");
      }
      element = document.getElementById("termandcondcheck2");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-check-input");
      }
    },
    applyCoupon: function() {
      var d = this.$data;
      if (d.coupon && d.coupon !== null && d.coupon !== "") {
        this.getInfo();
      }
    },
    getInfo: function() {
      var d = this.$data;
      var _this = this;
      d.loader = true;
      var bodyFormData = {
        language: d.language,
        ratekeys: this.$route.query.ratekeys,
        coupon: d.coupon
      };
      axios
        .post(globaldata.baseurl + "/hotel/pricedetails", bodyFormData, {
          headers: {
            "Content-Type": "application/json;charset=UTF-8"
          }
        })
        .then(function(response) {
          d.loader = null;
          d.detalles = response.data;
          if (!d.detalles.total) {
            d.detalles = null;
          }
          if (d.detalles) {
            console.log("entra en detalles");
            d.ocupacion = [];
            for (var i = 0; i < d.detalles.rateKeys.length; i++) {
              var ocup = {};
              var paxDetails = [];
              for (var j = 0; j < d.detalles.rateKeys[i].paxPerRoom; j++) {
                paxDetails.push({
                  name: null,
                  surname: null,
                  age: null,
                  type: null
                });
              }

              ocup.occupancy = paxDetails;
              ocup.roomId = i + 1;
              ocup.rateKey = d.detalles.rateKeys[i].key;
              ocup.roomName = d.detalles.rateKeys[i].roomName;
              ocup.requestPaymentData =
                d.detalles.rateKeys[i].requestPaymentData;

              if (d.detalles.rateKeys[i].requestPaymentData) {
                d.requestPayment = true;
              }
              d.ocupacion.push(ocup);
            }
          }
        })
        .catch(function(error) {
          console.log(error);
          d.loader = null;
        });
    },
    formatcomplementos: function() {
      var d = this.$data;
      d.selectedServices = "";

      for (var i = 0; i < this.detalles.availableServices.length; i++) {
        if (
          this.detalles.availableServices[i].cantidad &&
          this.detalles.availableServices[i].cantidad > 0
        ) {
          if (d.selectedServices !== "") {
            d.selectedServices = d.selectedServices.concat(
              ",",
              this.detalles.availableServices[i].id,
              "-",
              this.detalles.availableServices[i].cantidad
            );
          } else {
            d.selectedServices = d.selectedServices.concat(
              this.detalles.availableServices[i].id,
              "-",
              this.detalles.availableServices[i].cantidad
            );
          }
        }
      }
      if (d.selectedServices === "") {
        d.selectedServices = null;
      }
    },
    updatePaymentlines: function() {
      var d = this.$data;
      var _this = this;
      d.loader = true;
      this.formatcomplementos();
      var bodyFormData = {
        language: d.language,
        ratekeys: this.$route.query.ratekeys,
        coupon: d.coupon,
        selectedServices: d.selectedServices
      };
      axios
        .post(globaldata.baseurl + "/hotel/pricedetails", bodyFormData, {
          headers: {
            "Content-Type": "application/json;charset=UTF-8"
          }
        })
        .then(function(response) {
          d.loader = null;

          d.detalles.paymentLines = response.data.paymentLines;
          d.detalles.cancellationCosts = response.data.cancellationCosts;
          d.detalles.prices = response.data.prices;
          d.detalles.remarks = response.data.remarks;
          d.detalles.total = response.data.total;
        })
        .catch(function(error) {
          console.log(error);
          d.loader = null;
        });
    }
  },
  mounted() {
    this.$data.loader = true;
    this.getInfo();
    this.getStorageData();
    helper.scrollToId("main-app");
  }
};
</script>

