                            <template v-if="aloc.image">
                              <img
                                class="card-img-top"
                                :src="aloc.image"
                                :alt="aloc.roomDescription"
                              />
                            </template>
                            <template v-else>
                              <img class="card-img-top" :src="noimage" :alt="aloc.roomDescription" />
                            </template>
                          </div>
                          <div class="col-sm-12 col-lg-4 col-xl-6" style="margin:10px 0px;">
                            <h5 class="qt-room-title">{{ aloc.roomName }}</h5>
                            <div v-if="aloc.allotment < 5 && aloc.allotment > 1 "><span class="badge badge-warning text-white">{{multilanguage.only[language]}} {{aloc.allotment}} {{multilanguage.available[language]}}</span></div>
                            <div v-else-if="aloc.allotment === 1">
                              <span class="badge badge-danger">{{multilanguage.only[language]}} {{aloc.allotment}} {{multilanguage.available[language]}}</span>
                            </div>
                            <p class="card-text mt-1 mb-4">{{aloc.roomDescription}}</p>
                            <!--<div v-if=" aloc.prices[ocupacion.seleccionado].onRequest !== true"><span class="red-text">- {{price.onRequestText}}</span></div>-->
                          </div>
                          <div class="col-sm-12 col-lg-4 col-xl-3" style="margin:10px 0px;">
                            <div style="background:grey;  width:75%;height: 50%;float:right"></div>
                            </div>
                          <div class="col-sm-12 col-md-12 col-lg-12" style="margin:10px 0px;">
                          <!--  <h5 class="card-title">{{multilanguage.chooseboard[language]}}</h5>-->
                          <div class="qt-boards-title">{{multilanguage.chooseboard[language]}}</div>
                            <div class="list-group">
                              <button
                                @click="ocupacion.seleccionado = price.key;
                                              ocupacion.priceselected =
                                                price.retailPrice.value;
                                              ocupacion.selectedcurrency =
                                                price.retailPrice.currencyIsoCode;
                                              ocupacion.roomseleccionado = aloc.roomName;
                                              ocupacion.boardseleccionado = price.boardBasisName
                                              calculateTotals();
                                              openNext(ocupacionindex);
                                              isAllSelected();"
                                v-bind:class="{active : ocupacion.seleccionado === price.key}"
                                v-for="(price,indexroom) in aloc.prices"
