<template>
    <div>
        <nav>
          <div class="nav nav-tabs mt-3" id="nav-tab" role="tablist" >
            <a class="nav-item nav-link active" id="nav-fotos-tab" data-toggle="tab" href="#fotos" role="tab" aria-controls="nav-fotos" aria-selected="true">{{multilanguage.photos[language]}}</a>
            <a v-if="description" class="nav-item nav-link" id="nav-about-tab" data-toggle="tab" href="#about" role="tab" aria-controls="nav-about" aria-selected="false" >{{multilanguage.description[language]}}</a>
            <a v-if="ficha && ficha.length !== 0" class="nav-item nav-link" id="nav-features-tab" data-toggle="tab" href="#features" role="tab" aria-controls="nav-features" aria-selected="false" >{{multilanguage.features[language]}}</a>
            <a v-if="mapImage || (longitude && latitude && mapboxtoken)" v-on:click="maprender = true" class="nav-item nav-link" id="nav-onmap-tab" data-toggle="tab" href="#onmap" role="tab" aria-controls="nav-onmap" aria-selected="false" >{{multilanguage.map[language]}}</a>
