                    <div class="col-sm-6 primary-content">{{f.key}}</div>
                    <div class="col-sm-6 text-primary secondary-content" >{{f.value}}</div>
                  </div>
                </li>
              </ul>
            </div>

          </div>
          <div id="onmap" v-if="mapImage || (longitude && latitude)"  class="tab-pane fade show " role="tabpanel" aria-labelledby="nav-onmap-tab">
