              <img  class="carousel-box" v-bind:src="mapImage"  width="100%">
            </div>
            <div v-else>
              <template v-if="longitude && latitude">
                <div id='map' class="carousel-box" ></div>
              </template>
              <template v-else>
                <img v-bind:src="noimage"  class="carousel-box" >
              </template>
            </div>
          </div>

          <div id="fotos" class="tab-pane fade show active" role="tabpanel" aria-labelledby="nav-fotos-tab">
            <div v-if="imagenes.length !== 0" >

              <div id="carouselExampleControls" class="carousel slide carousel-box" data-ride="carousel">
                <div class="carousel-inner">
                  <div v-for="(img, indeximg) in imagenes" class="carousel-item " v-bind:class="{active : indeximg === 0}" :key="'e'+indeximg">
                    <img :src="img" class="d-block carousel-box" :alt="img">
                  </div>

                </div>
                <a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
                  <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                  <span class="sr-only">Previous</span>
                </a>
                <a class="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
                  <span class="carousel-control-next-icon" aria-hidden="true"></span>
                  <span class="sr-only">Next</span>
                </a>
              </div>

            </div>
            <div v-else>
              <div v-if="mainImage">
                <img v-bind:src="mainImage" class="carousel-box" >
              </div>
              <div v-else>
                <img v-bind:src="noimage" class="carousel-box" >
              </div>
            </div>
          </div>

        </div>
        </div>
</template>

<script>
import axios from 'axios'
import helper from '@/script/helper';

var globallanguage = 'es'
if (globaldata.language != null && globaldata.language != '') {
  globallanguage = globaldata.language
}

var iswhitebrand = false

if (globaldata.whitebrandhotel !== null && globaldata.whitebrandhotel !== '') {
  iswhitebrand = true
}

const langfile = require('../../assets/lang/content.json')
const noimage = require('../../assets/noimage.jpg')

export default {
  name: 'Ficha',
  data: function () {
    return {
        noimage: noimage,
       language: globallanguage,
      multilanguage: langfile.multilanguage,
      mapboxtoken: globaldata.mapboxtoken,
       description: null,
      longitude: null,
      latitude: null,
      ficha: [],
      imagenes: [],
      mainImage: null,
      mapImage: null,
      maprender: false
    }
  },
  props:['rate'],
  watch: {
    selectedValue: function (val) {
      this.refreshdates()
    },
   
    maprender: function (val) {
      var d = this.$data
      if (!d.mapImage) {
        this.createMap()
      }
    }
  },
  methods:{
      createMap: function () {
      var _this = this
      var d = this.$data

      if (d.latitude && d.longitude) {
        mapboxgl.accessToken = d.mapboxtoken

        var map = new mapboxgl.Map({
          container: 'map',
          style: 'mapbox://styles/mapbox/streets-v10',
          center: [d.longitude, d.latitude],
          zoom: 13
        })

        var el = document.createElement('div')
        el.className = 'marker'

        var marker = new mapboxgl.Marker()
          .setLngLat([d.longitude, d.latitude])
          .addTo(map)
      } 
      map.on('load', function() {
           map.resize()  
           })
      },
      getficha: function (key) {
      var d = this.$data
      
      axios({
        method: 'get',
        url: globaldata.baseurl + '/commons/datasheet/' + key,
        params: {
          language: d.language
        }

      })
        .then(function (response) {
      
          for (var i = 0; i < response.data.values.length; i++) {
            switch (response.data.values[i].key) {
              case 'name':
                d.title = response.data.values[i].value
                break
              case 'images/image':
                d.imagenes.push(response.data.values[i].value)
                break
              case 'images/main':
                d.mainImage = response.data.values[i].value
                break
              case 'images/map':
                d.mapImage = response.data.values[i].value
                break
              case 'description':
                d.description = response.data.values[i].value
                break
              case 'longitude':
                d.longitude = response.data.values[i].value
                break
              case 'latitude':
                d.latitude = response.data.values[i].value
                break
              case 'stars':
                if (response.data.values[i].value) { d.stars = parseInt(response.data.values[i].value) }
                break
              case 'keys':
                if (response.data.values[i].value) { d.keys = parseInt(response.data.values[i].value) }
                break
              default:
                var key = response.data.values[i].key
                var value = response.data.values[i].value
                d.ficha.push({key, value})
                break
            }
          }
        })
        .catch(function (error) {
          d.loader = null
          console.log(error)
        })
    }
  },
   mounted () {
    
    var d = this.$data
    this.getficha(this.rate)
   }
}
</script>
