<script>
import axios from "axios";
import wNumb from "wnumb";
import noUiSlider from "nouislider";
import helper from "@/script/helper";
import "nouislider/distribute/nouislider.css";
import Formulario from "@/components/hotel/Formulario";

var maptooltip = false;

function mouseover() {
  maptooltip = true;
}
var slider;

const langfile = require("../../assets/lang/content.json");
const noimage = require("../../assets/noimage.jpg");

export default {
  name: "HotelDispo",
  data: function() {
    return {
      noimage: noimage,
      viewmaptooltip: maptooltip,
      dispo: {},
      loader: null,
      viewmap: false,
      ListCalendar: globaldata.listcalendar,
      language: globaldata.language ? globaldata.language : "es",
      multilanguage: langfile.multilanguage,
      category: [true, true, true, true, true],
      pricesrange: null,
      maxPrice: null,
      minPrice: null,
      selectedValue: [
        {
          start: this.$route.query.entrada,
          end: this.$route.query.salida
        }
      ],
      themeStyles: {
        wrapper: {
          background: "#fff",
          border: "solid rgba(0, 0, 0, 0.2) 1px",
          borderRadius: "5px"
        },
        dayCell: {
          background: "#00C064",
          color: "#fafafa",
          padding: "0px",
          margin: "0px"
        },
        dayContent: {
          padding: "0px",
          margin: "0px"
        }
      },
      paginaactual: 1,
      totalpaginas: null,
      disableddates: [],
      availabledatesArray: [],
      isfirstpage: true,
      islastpage: false,
      indexSelectedCalendar: null,
      hidefilter: true,
      disableattr: {
        key: "disabled",

        highlight: {
          backgroundColor: "#F84744",
          opacity: 1,
          animated: false
        },
        // Just use a normal style
        contentStyle: {
          color: "white"
        }
      },
      enableattr: [
        {
          key: "available",

          highlight: {
            backgroundColor: "green",
            opacity: 1,
            animated: false
          },
          // Just use a normal style
          contentStyle: {
            color: "white"
          }
        }
      ],
      dragattr: {
        key: "drag",

        highlight: {
          backgroundColor: "#FFCA3B",
          opacity: 1,
          animated: false
        },
        // Just use a normal style
        contentStyle: {
          color: "black"
        }
      },
      selectattr: {
        key: "select",

        highlight: {
          backgroundColor: "#FFE259",
          opacity: 1,
          animated: false
        },
        // Just use a normal style
        contentStyle: {
          color: "black"
        }
      },

      formats: {
        title: "MMMM YYYY",
        weekdays: "W",
        navMonths: "MMM",
        input: ["L", "YYYY-MM-DD", "YYYY/MM/DD"], // Only for `v-date-picker`
        dayPopover: "L", // Only for `v-date-picker`
        data: ["L", "YYYY-MM-DD", "YYYY/MM/DD"] // For attribute dates
      }
    };
  },
  computed: {},
  components: {
    Formulario: Formulario
  },
  watch: {
    selectedValue: function() {
      var d = this.$data;
      if (d.indexSelectedCalendar !== null) {
        var clave = (d.paginaactual - 1) * 10 + d.indexSelectedCalendar;
        this.getSingleAvaiability(
          d.selectedValue[d.indexSelectedCalendar].start,
          d.selectedValue[d.indexSelectedCalendar].end,
          d.dispo.hotels[clave].hotelKey,
          clave
        );
      }
    },
    category: function() {
      this.fiterresults();
    },
    paginaactual: function() {
      if (this.$data.paginaactual === 1) {
        this.$data.isfirstpage = true;
        this.$data.islastpage = false;
      } else if (this.$data.paginaactual === this.$data.totalpaginas) {
        this.$data.isfirstpage = false;
        this.$data.islastpage = true;
      } else {
        this.$data.isfirstpage = false;
        this.$data.islastpage = false;
      }
    }
  },
  methods: {
    getSingleAvaiability: function(start, end, key, index) {
      var d = this.$data;
      var _this = this;
      axios({
        method: "get",
        url: globaldata.baseurl + "/hotel/available",
        params: {
          language: globaldata.language,
          resorts: key,
          checkin: this.formatDate(start).replace(/-/g, ""),
          checkout: this.formatDate(end).replace(/-/g, ""),
          occupancies: this.$route.query.ocupacion
        }
      })
        .then(function(response) {
          //  setTimeout(function () {
          d.dispo.hotels.splice(index, 1, response.data.hotels[0]);
          // })
        })

        .catch(function(error) {
          console.log(error);
        });
    },
    paginar(page) {
      var d = this.$data;
      d.indexSelectedCalendar = null;

      if (page < 1) {
        d.paginaacatual = 1;
      } else {
        if (page > d.totalpaginas) {
          d.paginaactual = d.totalpaginas;
        } else {
          d.paginaactual = page;
        }
      }
      d.selectedValue = [];
      if (globaldata.listcalendar === true) {
        this.getRoomsDispo(d.paginaactual);
      }
    },
    resetfilter() {
      var _this = this;
      var d = this.$data;

      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        slider.noUiSlider.reset();
      }

      d.category[0] = true;
      d.category[1] = true;
      d.category[2] = true;
      d.category[3] = true;
      d.category[4] = true;
      _this.getAvailability();
    },

    fiterresults() {
      var d = this.$data;
      var _this = this;
      d.loader = true;
      d.dispo = null;
      var parsedCategory = "";
      for (var j = 0; j < d.category.length; j++) {
        if (d.category[j]) {
          parsedCategory = parsedCategory + (5 - j) + ",";
        }
      }

      var prices = [null, null];
      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        prices = slider.noUiSlider.get();
      }

      var selectedLables = "";
      if (d.labels) {
        for (var i = 0; i < d.labels.length; i++) {
          if (d.labels[i]) {
            selectedLables = selectedLables + d.dispo.labels[i].id + ",";
          }
        }
      }

      axios({
        method: "get",
        url: globaldata.baseurl + "/hotel/filter",
        params: {
          language: globaldata.language,
          resorts: this.$route.query.iddestino,
          checkin: this.$route.query.entrada
            ? this.$route.query.entrada.replace(/-/g, "")
            : null,
          checkout: this.$route.query.salida
            ? this.$route.query.salida.replace(/-/g, "")
            : null,
          occupancies: this.$route.query.ocupacion,
          categories: parsedCategory,
          minprice: prices[0],
          maxprice: prices[1],
          labels: selectedLables
        }
      })
        .then(function(response) {
          d.loader = null;
          d.dispo = response.data;
          if (d.dispo.hotels) {
            d.totalpaginas = Math.ceil(d.dispo.hotels.length / 10);
            d.paginaactual = 1;
            if (globaldata.listcalendar === true) {
              _this.getRoomsDispo(d.paginaactual);
            }
          }
        })
        .catch(function(error) {
          d.loader = null;
          console.log(error);
        });
    },
    goback(page) {
      this.$router.push({
        name: "main",
        query: {
          destino: this.$route.query.destino,
          iddestino: this.$route.query.iddestino,
          salida: this.$route.query.salida,
          entrada: this.$route.query.entrada,
          ocupacion: this.$route.query.ocupacion
        }
      });
    },
    changeParameters() {
      this.$router.push({
        name: "main",
        query: {
          destino: this.$route.query.destino,
          iddestino: this.$route.query.iddestino,
          salida: this.$data.salida,
          entrada: this.$data.entrada,
          ocupacion: this.$route.query.ocupacion
        }
      });
    },
    mostrarmap() {
      var d = this.$data;
      d.viewmap = true;
    },
    deletetooltips() {
      var element = document.getElementsByClassName("material-tooltip"),
        index;
      for (index = element.length - 1; index >= 0; index--) {
        element[index].parentNode.removeChild(element[index]);
      }
    },
    abrirdetalle(hotelid, name, stars, keys, address, hotelkey) {
      this.deletetooltips();
      this.$router.push({
        name: "hotel_rates",
        query: {
          hotelkey: hotelkey,
          hotelid: hotelid,
          hotelname: name,
          stars: stars,
          keys: keys,
          address: address,
          salida: this.$route.query.salida,
          entrada: this.$route.query.entrada,
          ocupacion: this.$route.query.ocupacion,
          iddestino: this.$route.query.iddestino,
          destino: this.$route.query.destino
        }
      });
    },
    abrirdetalleCalendario(k, name, stars, keys, address, hotelkey, index) {
      if (
        !this.$data.selectedValue[index] ||
        this.$data.selectedValue[index] === null
      ) {
        //   M.toast({html: 'Seleccione fechas'})
      } else {
        this.$router.push({
          name: "hotel_rates",
          query: {
            hotelkey: k,
            hotelname: name,
            stars: stars,
            keys: keys,
            address: address,
            salida: this.formatDate(this.$data.selectedValue[index].start),
            entrada: this.formatDate(this.$data.selectedValue[index].end),
            ocupacion: this.$route.query.ocupacion,
            iddestino: this.$route.query.iddestino,
            destino: this.$route.query.destino
          }
        });
      }
    },
    /*CAMBIAR HOTELKEY EN MAPAS*/
    createmap(list) {
      var _this = this;
      mapboxgl.accessToken = globaldata.mapboxtoken;
      var map = new mapboxgl.Map({
        container: "map",
        style: "mapbox://styles/mapbox/streets-v9"
      });
      var destinox = this.$route.query.destino;
      var iddestinox = this.$route.query.iddestino;
      var salidax = this.$route.query.salida;
      var entradax = this.$route.query.entrad;
      var ocupacionx = this.$route.query.ocupacion;

      map.on("load", function() {
        var mapCanvas = document.getElementsByClassName("mapboxgl-canvas")[0];
        var mapDiv = document.getElementById("map");

        mapCanvas.style.width = mapDiv.style.width;
        mapCanvas.style.height = mapDiv.style.height;
        map.resize();
        var boundcordinate = [];
        var minlat = +100;
        var maxlat = -100;
        var minlong = +100;
        var maxlong = -100;
        var initURL = window.location.href.split("/#/");

        for (var i = 0; i < list.length; i++) {
          var markerHeight = 50,
            markerRadius = 10,
            linearOffset = 25;
          var popupOffsets = {
            top: [0, 0],
            "top-left": [0, 0],
            "top-right": [0, 0],
            bottom: [0, -markerHeight],
            "bottom-left": [
              linearOffset,
              (markerHeight - markerRadius + linearOffset) * -1
            ],
            "bottom-right": [
              -linearOffset,
              (markerHeight - markerRadius + linearOffset) * -1
            ],
            left: [markerRadius, (markerHeight - markerRadius) * -1],
            right: [-markerRadius, (markerHeight - markerRadius) * -1]
          };

          var div = window.document.createElement("div");
          var showcategory = "";

          if (parseInt(list[i].stars) > 0) {
            console.log("estrellas");
            for (var j = 0; j < parseInt(list[i].stars); j++) {
              showcategory =
                showcategory + '<i class="material-icons tiny">star</i>';
            }
          } else {
            for (var j = 0; j < parseInt(list[i].keys); j++) {
              showcategory =
                showcategory +
                '<i class="icon-rotate material-icons tiny">vpn_key</i>';
            }
          }
          var offerprice;
          if (list[i].bestDeal) {
            offerprice = '<div class="card-price">';
            if (list[i].bestDeal.offer !== true) {
              offerprice =
                offerprice +
                "<div>" +
                list[i].bestDeal.retailPrice.value +
                " &nbsp;" +
                list[i].bestDeal.retailPrice.currencyIsoCode +
                "</div></div>";
            } else {
              offerprice =
                offerprice +
                '<div class="offer-title">' +
                '<div class="valign-wrapper">' +
                _this.multilanguage.offerprice[_this.language] +
                ' &nbsp;  <i id="myButton" class="material-icons  " data-toggle="tooltip" data-placement="bottom" title="' +
                list[i].bestDeal.offerText +
                '" style="cursor: pointer;">new_releases</i>' +
                "</div>" +
                "</div>" +
                '<div class="right">';

              if (list[i].bestDeal.beforeOfferPrice) {
                offerprice =
                  offerprice +
                  '<span class="oldprice" >' +
                  list[i].bestDeal.beforeOfferPrice.value +
                  "</span> ";
              }
              offerprice =
                offerprice +
                '<span class="offer-price">' +
                list[i].bestDeal.retailPrice.value +
                "&nbsp;" +
                list[i].bestDeal.retailPrice.currencyIsoCode +
                "</span></div> </div></div></div>";
            }
          } else {
            offerprice =
              '<div class="card-price nodispo">' +
              _this.multilanguage.noavailablein[_this.language] +
              "</div>";
          }

          div.innerHTML =
            '<a class="deco-none" href = "' +
            initURL[0] +
            "/#/rates?hotelkey=" +
            list[i].hotelkey +
            "&hotelid=" +
            list[i].hotelId +
            "&hotelname=" +
            list[i].hotelName +
            "&destino=" +
            destinox +
            "&iddestino=" +
            iddestinox +
            "&salida=" +
            salidax +
            "&entrada=" +
            entradax +
            "&ocupacion=" +
            ocupacionx +
            "&stars=" +
            list[i].stars +
            "&keys=" +
            list[i].keys +
            "&kaddres=" +
            list[i].address +
            ' " style="cursor:pointer">\n' +
            '               <div id="cardmap" class="card cardcomplement" style="height: 95%">\n' +
            '<div class="card-image">' +
            '                  <img class=" card-img-top "  src="' +
            list[i].mainImage +
            '" alt="' +
            list[i].mainImage +
            '" >\n' +
            offerprice +
            "</div>" +
            '                  <div class="card-body  my-card-body-map">' +
            '                   <div class="card-title my-card-title-map">' +
            list[i].hotelName +
            '                   <div class="category">' +
            showcategory +
            "</div>" +
            " </div>" +
            '                   <div class="card-text my-card-text-map ">' +
            list[i].address +
            "</div>" +
            "                 </div>\n" +
            "                </div>\n" +
            "               </a>";

          var popup = new mapboxgl.Popup({
            offset: popupOffsets,
            className: "my-class"
          })
            .setLngLat([list[i].longitude, list[i].latitude])
            // .setHTML(body)
            .setDOMContent(div)
            .addTo(map);

          popup.on("open", function(e) {
            var card = document.getElementById("cardmap");

            card.addEventListener("click", function(e) {
              _this.deletetooltips();
            });
          });

          var el = document.createElement("div");
          el.className = "marker";

          var marker = new mapboxgl.Marker()
            .setLngLat([list[i].longitude, list[i].latitude])

            .setPopup(popup)
            .addTo(map);

          if (list[i].longitude < minlong) {
            minlong = list[i].longitude;
          }
          if (list[i].longitude > maxlong) {
            maxlong = list[i].longitude;
          }
          if (list[i].latitude < minlat) {
            minlat = list[i].latitude;
          }
          if (list[i].latitude > maxlat) {
            maxlat = list[i].latitude;
          }
        }

        boundcordinate = [[minlong, minlat], [maxlong, maxlat]];
        map.fitBounds(boundcordinate, { offset: [50, 50, 50, 50] });
      });
    },
    getAvailability: function() {
      var d = this.$data;
      var _this = this;
      d.loader = true;
      axios({
        method: "get",
        url: globaldata.baseurl + "/hotel/available",
        params: {
          language: globaldata.language,
          resorts: this.$route.query.iddestino,
          checkin: this.$route.query.entrada
            ? this.$route.query.entrada.replace(/-/g, "")
            : null,
          checkout: this.$route.query.salida
            ? this.$route.query.salida.replace(/-/g, "")
            : null,
          occupancies: this.$route.query.ocupacion
        }
      })
        .then(function(response) {
          d.loader = null;
          d.dispo = response.data;

          if (d.dispo.labels) {
            for (var i = 0; i < d.dispo.labels.length; i++) {
              d.labels[i] = false;
            }
          }

          d.minPrice = d.dispo.minPrice;
          d.maxPrice = d.dispo.maxPrice;

          if (d.dispo.hotels) {
            d.totalpaginas = Math.ceil(d.dispo.hotels.length / 10);
            d.paginaactual = 1;

            if (globaldata.listcalendar === true) {
              _this.getRoomsDispo(d.paginaactual);
            }
          }

          setTimeout(function() {
            if (
              d.maxPrice >= 0 &&
              d.minPrice >= 0 &&
              d.maxPrice !== d.minPrice
            ) {
              var step = 10;

              if (
                d.maxPrice - d.minPrice < 50 &&
                d.maxPrice - d.minPrice >= 20
              ) {
                step = 5;
              } else if (d.maxPrice !== d.minPrice < 20) {
                step = 1;
              }

              slider = document.getElementById("test-slider");
              if (!slider.noUiSlider) {
                noUiSlider.create(slider, {
                  start: [d.minPrice, d.maxPrice],
                  connect: true,
                  step: step,
                  tooltips: [true, true],
                  pips: {
                    mode: "count",
                    values: 5,
                    density: 5
                  },
                  orientation: "horizontal", // 'horizontal' or 'vertical'
                  range: {
                    min: d.minPrice,
                    max: d.maxPrice
                  },
                  format: wNumb({
                    decimals: 0
                  })
                });
                slider.noUiSlider.on("change", function(values, handle) {
                  _this.fiterresults();
                });
              }
            }
          });
        })
        .catch(function(error) {
          d.loader = null;
          console.log(error);
        });
    },
    getRoomsDispo(actual) {
      var d = this.$data;
      d.availabledatesArray = [];
      var inicio = (actual - 1) * 10;

      for (var i = (actual - 1) * 10; i < (actual - 1) * 10 + 10; i++) {
        this.getavailabledates(d.dispo.hotels[i].hotelKey);
      }
    },
    getavailabledates(destino) {
      var d = this.$data;
      var _this = this;
      d.loader = true;
      axios({
        method: "get",
        url: globaldata.baseurl + "/cms/hotelavailabilitycalendar",
        params: {
          resorts: destino,
          checkIn: this.$route.query.entrada
            ? this.$route.query.entrada.replace(/-/g, "")
            : null,
          checkout: this.$route.query.salida
            ? this.$route.query.salida.replace(/-/g, "")
            : null,
          occupancies: this.$route.query.ocupacion
        }
      })
        .then(function(response) {
          var isFirst = true;
          var startdate;
          var enddate;
          var lastdate;
          var lastdateTrans;
          var availabledates = [];
          var disponibilidad;

          d.loader = null;
          disponibilidad = response.data;
          for (var i = 0; i < disponibilidad.months.length; i++) {
            for (var j = 0; j < disponibilidad.months[i].weeks.length; j++) {
              for (
                var k = 0;
                k < disponibilidad.months[i].weeks[j].days.length;
                k++
              ) {
                if (isFirst) {
                  startdate = disponibilidad.months[i].weeks[j].days[k].date;
                  enddate = null;
                  isFirst = false;
                } else {
                  var actualDate = new Date(
                    disponibilidad.months[i].weeks[j].days[k].date
                  );
                  actualDate.setDate(actualDate.getDate() - 1);
                  actualDate.setMilliseconds(0);
                  actualDate.setSeconds(0);
                  actualDate.setMinutes(0);
                  actualDate.setHours(0);
                  if (actualDate.getTime() !== lastdateTrans.getTime()) {
                    var ldate = lastdateTrans;
                    ldate.setDate(ldate.getDate() + 1);
                    enddate = _this.formatDate(ldate);
                    availabledates.push({ start: startdate, end: enddate });
                    startdate = disponibilidad.months[i].weeks[j].days[k].date;
                  } else {
                  }
                }
                lastdate = disponibilidad.months[i].weeks[j].days[k].date;
                lastdateTrans = new Date(
                  disponibilidad.months[i].weeks[j].days[k].date
                );

                lastdateTrans.setMilliseconds(0);
                lastdateTrans.setSeconds(0);
                lastdateTrans.setMinutes(0);
                lastdateTrans.setHours(0);
              }
            }
          }
          var ldate = new Date(lastdate);
          ldate.setDate(ldate.getDate() + 1);
          enddate = _this.formatDate(ldate);
          availabledates.push({ start: startdate, end: enddate });

          d.availabledatesArray.push(availabledates);
        })
        .catch(function(error) {
          d.loader = null;
          d.availabledatesArray.push(0);
          console.log(error);
        });
    },
    formatDate: function(date) {
      var d = new Date(date);
      var month = "" + (d.getMonth() + 1);
      var day = "" + d.getDate();
      var year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    display(index) {}
  },
  updated() {
    var d = this.$data;
    var _this = this;

    if (d.viewmap && d.dispo && d.dispo.hotels) {
      _this.createmap(d.dispo.hotels);
    }
  },
  mounted() {
    this.getAvailability();
    helper.scrollToId("main-app");
  }
};
</script>

